@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Poppins';
}
#skills {
    height: 50vh;
    width: 100%;
    background-color: #e6d8d1;
}
h1 {
    position: absolute;
    margin-bottom: 15%;
    background-color: #846D62;
    color: #faf7f5;
}
h3 { 
    background-color: #846D62;
    color: #faf7f5;
}
.my-skills {
    display: flex;
    width: 90%;
    justify-content: center;
    align-items: center;
    gap: 20px;
    box-shadow: -1px 4px 17px -3px rgba(0,0,0,0.74);
    margin-left: 5%;
    position: absolute;
    margin-top: 6%;
    border-radius: 20px;
    background-color: #846D62;
}
.skills {
    margin-top: 5%;
    display: grid;
    place-items: center;
    width: 100%;
    padding: 10px;
    padding-bottom: 30px;
    border-radius: 20px;
    background-color: #846D62;
    animation: appear-left 1s ease-in;
}
@keyframes appear-left {
    0% {
        margin-left: -2%;
        opacity: 0;
    }
    100% {
        margin-left: 0%;
        opacity: 1;
    }
}
.skills2 {
    margin-top: 5%;
    display: grid;
    place-items: center;
    width: 100%;
    padding: 10px;
    padding-bottom: 30px;
    border-radius: 20px;
    background-color: #846D62;
    animation: appear-right 1s ease-in;
}
@keyframes appear-right {
    0% {
        margin-right: -2%;
        opacity: 0;
    }
    100% {
        margin-right: 0%;
        opacity: 1;
    }
}

.html, .css, .js, .php, .mysql, .react, .wp, .node {
    border: 1px solid black;
    width: 100%;
    height: 15px;
    border-radius: 20px;
}
.process-html {
    background-color: rgb(231, 184, 96);
    width: 90%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress1 1s ease-out;
}
.process-css {
    background-color: rgb(231, 184, 96);
    width: 80%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress4 1s ease-out;
}
.process-js {
    background-color: rgb(231, 184, 96);
    width: 60%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress2 1s ease-out;
}
.process-php {
    background-color: rgb(231, 184, 96);
    width: 65%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress 1s ease-out;
}
.process-mysql {
    background-color: rgb(231, 184, 96);
    width: 60%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress2 1s ease-out;
}
.process-react {
    background-color: rgb(231, 184, 96);
    width: 60%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress2 1s ease-out;
}
.process-wp {
    background-color: rgb(231, 184, 96);
    width: 30%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress3 1s ease-out;
    transition: 1s;
}
.process-node {
    background-color: rgb(231, 184, 96);
    width: 30%;
    height: 13px;
    border-radius: 20px;
    z-index: -1;
    animation: progress3 1s ease-out;
    transition: 1s;
}
@keyframes progress {
    from {width: 0%;}
    to {width: 65%;}
}
@keyframes progress1 {
    from {width: 0%;}
    to {width: 90%;}
}
@keyframes progress2 {
    from {width: 0%;}
    to {width: 60%;}
}
@keyframes progress3 {
    from {width: 0%;}
    to {width: 30%;}
}
@keyframes progress4 {
    from {width: 0%;}
    to {width: 80%;}
}
@media only screen and (max-width: 480px) {
    * {
        font-size: 16px;
    }
    h1 {
        margin-bottom: 60%;
        position: absolute;
        font-size: 25px;
    }
    .my-skills {
        height: 42%;
    }

}
@media only screen and (min-width: 481px) and (max-width: 768px){
    .my-skills {
        height: 43%;
    }
    h1 {
        margin-bottom: 60%;
        position: absolute;
    }
}