@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Poppins';
}
#home {
    height: 61vh;
    width: 100%;
}
.my-name{
    margin-top: 20%;
    margin-left: 15%;
    font-weight: bold;
}
.hello, .expert, .hire {
    animation: opac 2s ease-out;
}
.name {
    animation: surprise 1s ease-out;
}
@keyframes opac {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes surprise {
    0% {
        margin-left: -10%;
        opacity: 0;
    }
    100% {
        margin-left: 0%;
        opacity: 1;
    }
}
.hello {
    font-size: 25px;
    color: #707070;
}
.name {
    font-size: 50px;
    font-weight: bold;
}
.expert {
    font-size: 35px;
    color: #707070;
}
.hire {
    padding: 10px;
    border-radius: 20px;
    margin-right: 1%;
    margin-top: 1%;
    color: white;
    background-color: #2D2D2D;
    cursor: pointer;
    transition: 0.3s;
}
.hire:hover {
    transform: scale(1.08);
}
@media only screen and (max-width: 480px) {
    .name {
        font-size: 60px;
    }

}