.content {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(132,109,98);
    height: 30vh;
}
.content h1 {
    font-size: 30px;
}
@media only screen and (max-width: 480px) {
    .content h1 {
        position: absolute;
        margin-top: 35%;
        font-size: 25px;
    }
}
@media only screen and (min-width: 481px) and (max-width: 768px){
    .content h1 {
        margin-top: 32%;
        font-size: 25px;
        position: absolute;
    }
}
